body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
}

.header-line {
  border: 0;
  width: 100%;
  height: 2px;
  background: gray;
}

.header-button {
  display: flex;
  align-items: center;
  margin-left: 5px;
  margin-right: 5px;
}
.side {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 160px;
  position: flex;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: white;
  overflow-x: hidden;
  padding-top: 20px;
}

.side-div-line {
  display: flex;
  height: 100%;
  width: 2px;
  z-index: 2;
  border-style: thick;
  border-color: blueviolet;
  background-color: black;
}

.side-items {
  display: flex;
  align-items: center;
  background-color: aquamarine;
}

.side-container {
  z-index: 5;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-right: 2px black;
  margin-right: 10px;
  background-color: blue;
}
.small-title {
  border-bottom: 1px solid #688FF4; 
  padding: 0.1em;
}
.column-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.row-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.div-line {
  display: flex;
  height: 100%;
  width: 2px;
  border-style: thick;
  border-color: black;
  background: gray;
}
